@charset "utf-8";
/*通用样式*/
*{ margin:0;padding:0;list-style:none;
}
html {background: #f8f8f8;
}
* html body{ overflow:visible;
}
* html iframe, * html frame{ overflow:auto;
}
* html frameset{ overflow:hidden;
}
body{ color:#000; background:#FFF; font:13px/22px Verdana,Arial,sans-serif,"Times New Roman",宋体; text-align:center;
}
body,ul,ol,li,p,h1,h2,h3,h4,h5,h6,form,fieldset,table,td,img,div,tr{ margin:0; padding:0;
}
input,select{ font-size:15px; vertical-align:middle;
}
input,textarea,select,a:focus { outline: none;
}
body div{ text-align:left;
}
textarea,input{ word-wrap:break-word; word-break:break-all; padding:0px;
}
li{ list-style-type:none;
}
img{ border:0 none;
}
a:link, a:visited{  text-decoration:none;
}
a:hover{ text-decoration:none;cursor: pointer;
}
a { outline:none; star:expression(this.onFocus=this.blur());
}
a:-webkit-any-link{color:#eee;text-decoration:none;
}
/**
  通用class名：
  .active(激活状态)  .a(a链接)  .btn(按钮)  .bg(背景)  .txt(文本)  .border(边框) .view-content(路由显示区的顶部距离)
  .theme1.theme2.theme3(三种主题背景色调)
**/
button,input{
    transition:  0.3s;
    -moz-transition:  0.3s;
    -webkit-transition:  0.3s;
    -o-transition:  0.3s;
}
