  width: 120px;
  height: 25px;
  padding-left: 8px;
  border-radius: 3px;
  border: 1px solid #ccc;
  color: #606266;
}
.btn{
  border:none;
  border-radius: 6px;
  outline: none;
  font-size: 15px;
  cursor: pointer;
  line-height: 30px;
  color: #fff;
  padding:1px 15px;
}
