.nav-list li .active{
  position:relative;
  color:#fff;
}
.nav-list .active .close{
  color:#eee;
  display: inline-block;
}
.nav-list li .close{
  display:inline-block;
  margin: 0px -6px 0 1px;
  width:18px;
  height:17px;
  line-height:18px;
  text-align:center;
  border-radius:50%;
  color:#eee;
  cursor:pointer;
  font-size:1px;
  position: relative;
  top: -1px;
  font-size: 12px;
  transition: 0.2s;
  -moz-transition: 0.2s;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
}
.nav-list li .close:hover{
  background:#ddd;
  color:#fff;
}
.userBox>div{
  float: right;
}
.theme-box b{
  display: inline-block;
  width: 32px;
  height: 32px;
  border-radius: 4px;
  cursor: pointer;
}
#themeB{
  display: inline-block;
  line-height: 32px;
  text-align: center;
  font-size: 12px;
  color: #fff;
  transition:  0.4s;
  -moz-transition:  0.4s;
  -webkit-transition:  0.4s;
  -o-transition:  0.4s;
}
.theme-box{
  position: relative;
  right: 106px;
  top: 18px;
  padding-bottom: 16px;
}
.chge-them{
  position: absolute;
  top: 38px;
  left: -6px;
  z-index: 100;
  border: 1px solid #ddd;
  border-radius: 4px;
  background: #fff;
  padding: 7px 5px 2px 5px;

  display: none;
}
.theme-box:hover .chge-them{
  display: block;
}
.log-box{
  position: relative;
  right: -11px;
  top: 10px;
  cursor: pointer;
  padding-bottom: 13px;
}
.loge-img{
  display: inline-block;
  padding: 2px;
  border: 1px solid #ddd;
  border-radius: 3px;
}
.log-box>b{
  display: inline-block;
  position: relative;
  top: 13px;
  right: -8px;
  width:0;
  border-left:4px solid transparent;
  border-right:4px solid transparent;
  border-top:7px solid #999;
  margin-top: 10px;
}
.log-menu{
  position: absolute;
  top: 55px;
  right: 13px;
  background: #fff;
  border: 1px solid #ddd;
  -webkit-box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
          box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
  border-radius: 4px;
  display: none;
}
.log-menu>b{
  display: inline-block;
  border-top: 1px solid;
  border-right: 1px solid;
  width: 6px;
  height: 6px;
  border-color: #ddd;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  background: #fff;
  position: absolute;
  top: -4px;
  right: 15px;
}
.log-menu>a{
  display: block;
  width: 110px;
  line-height: 30px;
  color: #666;
  text-align: center;
}
.log-box:hover .log-menu{
  display: block;
}
/**** 表格页面公用样式 ****/
.query-head{
  padding: 20px 60px;
  color: #606266;
}
.query-head>label{
  margin-left: 20px;
}
.btn-box{
  display: inline-block;
  margin-left: 13px;
}
.btn-box>button{
  margin-left: 10px;
}
.query-msg-box{
  margin: 10px 0 30px 0;
}
.query-msg-box table{
  margin: 0 auto;
  width: 95%;
  border-top: 1px solid #cdd2de;
  border-left: 1px solid #cdd2de;
  text-align: center;
  background: #fff;
}
.query-msg-box table td,.query-msg-box table th{
    border-right: 1px solid #cdd2de;
    border-bottom: 1px solid #cdd2de;
}
.query-msg-box table td{
  height: 32px;
  padding: 5px 0;
}
.query-msg-box table thead tr{
  background: #f5f7fa;
}
.query-msg-box table tbody tr:nth-child(even){
  background: #f5f7fa;
}
.query-msg-box table th{
  line-height: 35px;
}
.query-msg-box thead{
  color: #65676b;
}
.query-msg-box tbody{
  color: #606266;
}
#pagination-box{
  margin: 10px 0 105px 55px;
  color: #606266;
}
#pagination-box>label{
  margin-left: 20px;
}
#pagination-box .select-box{
  width:65px;margin-left: 20px;
}
.sel-lef,.sel-rig{
  display: inline-block;
  position: relative;
  top: 11px;
  width: 25px;
  height: 25px;
  border: 1px solid #ccc;
  border-radius: 3px;
  margin: 3px;
  cursor: pointer;
  background: url(data:image/png;base64,)no-repeat 5px 5px;
}
.sel-lef{
  margin-left: 25px;
  transform:rotate(90deg);
  -ms-transform:rotate(90deg);
  -moz-transform:rotate(90deg);
  -webkit-transform:rotate(90deg);
  -o-transform:rotate(90deg);
}
.sel-rig{
  transform:rotate(-90deg);
  -ms-transform:rotate(-90deg);
  -moz-transform:rotate(-90deg);
  -webkit-transform:rotate(-90deg);
  -o-transform:rotate(-90deg);
}
#goToPag{
  width: 40px;
  position: relative;
  top: -1px;
  text-align: center;
  padding: 0px;
}
/*详情弹出框*/
#detailBox{
  position: absolute;
  top: 135px;
  left: 185px;
  z-index: 500;
  width: 800px;
  height: 400px;
  background: #fff;
  border-radius: 4px;
  -webkit-box-shadow: 0 2px 15px 0 rgba(0,0,0,.5);
          box-shadow: 0 2px 15px 0 rgba(0,0,0,.5);
  font-size: 15px;
  display: none;
}
#detailBox h2{
  margin: 35px 10px;
  text-align: center;
}
#detailBox p{
  line-height: 50px;
}
#detailBox label{
  display: inline-block;
  width: 130px;
  text-align: right;
  margin-left: 100px;
}
#detailBox span{
  display: inline-block;
  width: 150px;
  margin-left: 5px;
}
#detailBox .spanL{
  width: 480px;
}
/*页面过场动画*/
.fade-enter {
  opacity:0;
}
.fade-leave{
  opacity:1;
}
.fade-enter-active{
  -webkit-transition:opacity .3s;
  transition:opacity .3s;
}
.fade-leave-active{
  opacity:0;
  -webkit-transition:opacity .3s;
  transition:opacity .3s;
}
























































